'use-strict'
var view = require('./view'),
		Vdom = require('./vdom'),
		Calc = require('./calc')

var calc = Calc()

function render() {
	var data = calc.next()
	var vdom = Vdom(data.value)
	window.requestAnimationFrame(function(ms) {
		view(vdom)
		if (!data.done) render()
	})
}

render()



